@charset "UTF-8";
html, body {
    width: 100%;
}
section,header,footer{display:block;}
#book-style-box{
    padding-bottom:5.9rem;
    .book-style-boy {
        .book-style-blist{
            border-bottom: 1px solid #ccc;
            display: -webkit-box;
            display: -webkit-flex;
            display: flex; 
            li{
                -webkit-box-flex: 1;
                -webkit-flex: 1;
                flex: 1;
                text-align: center;margin:30px 0 25px;  
                a{
                    display:block;
                    width:6rem;
                    margin:0 auto;
                    border-radius: 50%; 
                    img{
                        display:block;
                        width: 6rem;
                    }
                } 
                h2{
                    font-size: 1.4rem;padding-top:10px;
                    text-align: center;
                    color: #666;
                    font-weight: bold;
                }
            }
            &.bor-none{
                border-bottom:none;
            }
        }
        &:last-child{
            border: 0;
        }
        &:nth-child(1){
            margin: 0;
        }
    }

}

.book-mod-none{
    display: none;
}

.pin-footer {
    border-top: 1px solid #bbb;
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    height: 49px;
    z-index: 10;
    overflow: hidden;
    background:#fafafa;
    .home-goback {
        float: left;
        height: 49px;
        padding-top:14px;
        width:18%;
        border-right: 1px solid #bbb;
        img{
            display:block;
            width: 2rem;
            margin: 0  auto;
        }
    }
    .pin-footer-tab{
        float: left;
        width:80%;
        height: 49px;
        li{
            float: left;
            width: 33%;
            line-height: 49px;
            text-align: center;
            border-right: 1px solid #bbb;
            font-size:1.6rem;
            color:#666;
            letter-spacing: 0.4rem;
            &:nth-child(3){
                border: 0;
            }
            &.footer-tab-active{
               color: #6cd; 
            }
        }
    }
}

